function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        effect: 'fade',
        autoplay: true,
        allowTouchMove: false,
        fadeEffect: {
            crossFade: true
        },
    })
}

function profiSlider() {
    var swiper = new Swiper('.profi-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        effect: 'fade',
        autoplay: true,
        allowTouchMove: false,
        fadeEffect: {
            crossFade: true
        },
    })
}

function videosSlider() {
    var swiper = new Swiper('.videos .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        pagination: {
            el: '.videos .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function scienceSlider() {
    var swiper = new Swiper('.science .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 0,
        centeredSlides: false,
        loop: true,
        pagination: {
            el: '.science .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints:{
            320: {
                centeredSlides: true,
                slidesPerView: 1,
            },
            993: {
                centeredSlides: false,
                slidesPerView: 3,
            }
        }
    })
}

let philosophySlidersArray = []

function philosophySliders() {
    $(".philosophy-card").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        philosophySlidersArray[index] = new Swiper(`.philosophy-card[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 1,
            spaceBetween: 0,
            pagination: {
                el: `.philosophy-card[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

function reviewSlider() {
    var swiper = new Swiper('.review .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        pagination: {
            el: '.review .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

videosSlider()
scienceSlider()
philosophySliders()
reviewSlider()
mainSlider()
profiSlider()
